import { Component, ViewChild, ElementRef } from '@angular/core';
import { IonicPage, NavController, NavParams } from 'ionic-angular';
import { BranchMuseumsServiceProvider } from '../../providers/branch-museums-service/branch-museums-service'
import * as app_globals from '../../app/app-globles';
declare var google;


/**
 * Generated class for the BranchMuseumsPage page.
 *
 * See https://ionicframework.com/docs/components/#navigation for more info on
 * Ionic pages and navigation.
 */

@IonicPage()
@Component({
  selector: 'page-branch-museums',
  templateUrl: 'branch-museums.html',
})
export class BranchMuseumsPage {
  public branches: any = [];
  public contentURL: any;
  @ViewChild('map') mapElement: ElementRef;
  map: any;
  otherLocation: any = [];

  constructor(public navCtrl: NavController, public navParams: NavParams, private branchMuseumsServiceProvider: BranchMuseumsServiceProvider) {

  }

  ionViewDidLoad() {
    this.contentURL = app_globals.k_content_url;
    var defaultLocation = { lat: 6.9105748, lng: 79.8599694 };
    this.map = new google.maps.Map(this.mapElement.nativeElement, {
      zoom: 7,
      center: defaultLocation
    });
    this.branchMuseumsServiceProvider.getAll().subscribe(
      successResponse => {
        let selected: any = successResponse;
        for (let branch in selected.museums) {
          this.branchMuseumsServiceProvider.getById(selected.museums[branch].id, 'en').subscribe(
            successResponse2 => {
              let val: any = successResponse2;
              this.branches.push(val);

              var position = new google.maps.LatLng(val.latitude, val.longitude);
              var museumMarker = new google.maps.Marker({ position: position, title: val.name });
              museumMarker.setMap(this.map);


            });
        }
      });
  }
}



